body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
#404040 // reto
#595959 //cuadro
#33CC33 verde boton
#FFC000 amarrilo bolita
#92D050 verde bolita
*/
.container-nav {
    box-shadow: 2px 7px 18px -12px rgba(0,0,0,0.75);
    background: #fff;
}

.nav {
    width: 80%;
    height: 70px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.name-development {
    font-size: 1.2rem;
    display: block;
}

.link-nav-msg {
    color: #22C4F6;
    display: block;
    font-size: 1.2rem;
}

.link-nav-name {
    color: #595959;
    font-weight: bold;
}
.inputTask {
    border: 1px solid #D9D9D9;
    border-radius: 10px;
    padding: 5px 10px;
    margin-top: 10px;
}

.container-state-task {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.container-task-ok {
    display: flex;
    align-items: center;
}
.task-ok {
    border: 1px solid #D9D9D9; 
    border-radius: 50%;
    cursor: pointer;
    margin-right: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 30px;
}

.imagen-ok {
    width: 100%;
    height: 80%;
    padding-top: 5px;
}

.name-input-task {
    display: inline-block;
    font-size: 1.1rem;
}

.remove {
    color: red;
    cursor: pointer;
}
.task {
    border: 1px solid #22C4F6;
    border-radius: 10px;
    width: 300px;
    height: 420px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.49);
    background:#fff;
}

.task-body {
    padding: 10px 20px;
}

.container-task-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.name-category-task {
    color: #404040;
    font-weight: bold;
    font-size: 1.3rem;
}

.task-figure {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
}

.container-task-content {
    overflow: auto;
    height: 290px;
    padding-right: 10px;
}

.container-task-content::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }

 .container-task-content::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 6px;
  }
  
  .container-task-content::-webkit-scrollbar-thumb:active {
    background-color: #999999;
  }
  
  .container-task-content::-webkit-scrollbar-thumb:hover {
    background: #b3b3b3;
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
  }
  
  .container-task-content::-webkit-scrollbar-track {
    background: #e1e1e1;
    border-radius: 4px;
  }

.container-task-footer {
    border-top: 1px solid #22C4F6;
    padding: 10px;
    box-shadow: 0px -2px 2px 0px rgba(0,0,0,0.15);
    display: flex;
    justify-content: space-between;
}

.task-form-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.input-task {
    border: 1px solid #22C4F6;
    border-radius: 50px;
    height: 30px;
    padding: 2px 10px;
    font-size: 1rem;
    outline: none;
}

.content-submit {
    border: 1px solid #22C4F6;
    padding: 5px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.submit-task {
    color: transparent;
    background-color: transparent;
    border: none;
    cursor: pointer;
    background-image: url(/repo/static/media/send-button.0a9e12f0.svg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 28px;
    width: 28px;
    padding: 0;
    margin-right: -2px;
    margin-left: 2px;
    outline: none;
}

body {
    background: #F6F6F6;

}
.container-content {
    width: 80%;
    margin: 0 auto;
    padding-top: 10px;
    margin-top: 20px;
}

.title-content {
    color: #404040 ;
    font-size: 1.5rem;
}

.container-category {
    display: flex;
    justify-content: space-between;
}
